<template>
  <body :class="{ debug: isDev }">
    <header>
      <navbar v-if="user" :user="user"></navbar>
      <navbar-guest v-else></navbar-guest>
    </header>

    <router-view></router-view>

    <footer>
    </footer>

    <div v-if="isDev" id="dev-toolbox">
      <b-btn v-b-toggle.collapseDevToolbox variant="outline-danger">* Dev Toolbox</b-btn>
      <b-collapse id="collapseDevToolbox" class="mt-2">
        <b-card>
          <button type="button" class="btn btn-danger btn-sm" @click="test">Test Action</button>
          <div slot="footer">
            {{ buildTimestamp }}
          </div>
        </b-card>
      </b-collapse>
    </div>

  </body>
</template>


<script>
import Navbar from './components/Navbar'
import NavbarGuest from './components/NavbarGuest'

export default {
  components: {
    Navbar,
    NavbarGuest,
  },
  data() {
    return {
      isDev: process.env.APP_DEBUG,
      buildTimestamp: process.env.BUILD_TIMESTAMP,
    }
  },
  methods: {
    test() {
      console.log('Test')
    },
  },
  computed: {
    user() {
      // return this.$store.getters.getLoggedUser
    },
  },
}
</script>



