<template>
  <table>
    <thead>
      <tr>
        <th>Name</th>
      </tr>
    </thead>

    <tbody>
      <tr v-for="company in companies">
        <td>{{ company.name }}</td>
      </tr>
    </tbody>
  </table>
</template>


<script>
export default {
  props: ['companies'],
}
</script>



